.box {
    margin: 0;
    padding-top: 70%;
    height: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-top: .7mm solid #FFFFFF;
    border-bottom: 2.5mm solid #FFFFFF;
    color: #FFFFFF;
    position: relative;
}

.box-title {
    margin: 0;
    padding-top: 25%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}